import { Entity } from '@backstage/catalog-model';

export const KEPTN_PROJECT_KEY_ANNOTATION = 'keptn.sh/project';
export const KEPTN_SERVICE_KEY_ANNOTATION = 'keptn.sh/service';

export const isProjectAvailable = (entity: Entity) =>
  Boolean(entity.metadata.annotations?.[KEPTN_PROJECT_KEY_ANNOTATION]);

export const isServiceAvailable = (entity: Entity) =>
  Boolean(entity.metadata.annotations?.[KEPTN_PROJECT_KEY_ANNOTATION]) &&
  Boolean(entity.metadata.annotations?.[KEPTN_SERVICE_KEY_ANNOTATION]);

export const useProjectKey = (entity: Entity) => {
  return entity?.metadata.annotations?.[KEPTN_PROJECT_KEY_ANNOTATION] ?? '';
};

export const useServiceKey = (entity: Entity) => {
  return entity?.metadata.annotations?.[KEPTN_SERVICE_KEY_ANNOTATION] ?? '';
};
